body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main{
  width: 1024px;
  margin: 0 auto;
}

.city-selector{
  width: 280px;
  margin: 30px auto;
}

#selector-box{
  margin-left: 10px;
}

.location-info{
  width: 200px;
  margin: 0 auto;
}

table{
  margin-top: 30px;
}

th{
  padding: 10px 30px;
  width: 1000px;
  font-weight: normal;
}

.date{
  font-weight: bold;
}

.temperature-max{
  color: red;
}

.temperature-min{
  color: blue;
}

.description-area{
  width: 500px;
  margin-top: 30px;
  float: left;
}

.description-title{
  font-weight: bold;
}

.pinpoint-locations-area{
  width: 500px;
  height: 500px;
  margin-top: 30px;
  float: right;
}

.pinpoint-locations-title{
  font-weight: bold;
}

li{
  float: left;
  margin-left: 20px;
}

